import { request } from 'utils';

const prefix = '/api/categories';

export const getDormitorys = ({
  label,
  noOfFloor,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Dormitory/GetDormitoryCollection',
    method: 'GET',
    params: { label, noOfFloor, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getDormitoryById = id =>
  request({
    prefix,
    url: `/Dormitory/GetDormitoryById`,
    params: { id },
    method: 'GET',
  });

export const deleteDormitory = id =>
  request({
    prefix,
    url: `/Dormitory/DeleteDormitory`,
    data: { id },
    method: 'DELETE',
  });

export const updateDormitory = ({ id, label, noOfFloor }) =>
  request({
    prefix,
    url: `/Dormitory/UpdateDormitory`,
    data: { id, label, noOfFloor },
    method: 'PUT',
  });

export const createDormitory = ({ id, label, noOfFloor }) =>
  request({
    prefix,
    url: `/Dormitory/CreateDormitory`,
    data: { id, label, noOfFloor },
    method: 'POST',
  });

export const getDormitoryFloors = ({
  dormId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory/GetDormitoryFloorCollection`,
    method: 'GET',
    params: { dormId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



